import {isValidURL} from "../../lib/src/index.js";
import {useEffect, useState} from "../../../pkg/react.js";
import {useStore} from "./hooks.js";
import {useLocalStorage} from "./useLocalStorage.js";
export const useBaseURL = () => {
  const store = useStore();
  const [baseURLJSON, setBaseURLJSON] = useLocalStorage("baseUrl", store.getBaseUrl());
  const [baseURL, setBaseURL] = useState(window.location.origin);
  useEffect(() => {
    if (baseURL !== null) {
      if (isValidURL(baseURLJSON)) {
        setBaseURL(baseURLJSON);
      } else {
        store.handleError(new Error(`Invalid base URL: ${baseURLJSON}, defaulting to atomicdata.dev`));
        setBaseURL("https://atomicdata.dev");
      }
    }
  }, [baseURLJSON]);
  useEffect(() => {
    store.setBaseUrl(baseURL);
  }, [baseURL]);
  return [baseURL, setBaseURLJSON];
};
